.about-us {
  background-color: $color-background-default;
  position: relative;

  .container {
    max-width: 100%;
    position: relative;
    z-index: 3;
    padding-left: 0;
    padding-top: 50px;
    overflow: clip visible;
    display: grid;
    grid-template-columns: auto 1fr;

    img {
      margin-top: -35px;

      @include vp-1199 {
        margin-top: 7px;
      }

      @include vp-767 {
        margin-top: -9px;
      }
    }


    @include vp-1199 {
      padding-top: 0;
    }

    @include vp-767 {
      grid-template-columns: 1fr;
      padding-right: 0;
      gap: 47px;
    }
  }
}

h3 {
  margin-top: 0;
  margin-bottom: 16px;
  font-weight: 700;
  font-size: 21px;
  line-height: 21px;

  @include vp-1199 {
    margin-bottom: 14px;
    font-size: 18px;
    line-height: 18px;
  }

  @include vp-767 {
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 16px;
  }
}

p {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 22px;

  &:not(:last-of-type) {
    margin-bottom: 30px;

    @include vp-1199 {
      margin-bottom: 30px;
    }

    @include vp-767 {
      margin-bottom: 43px;
    }
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 20px;
  }
}

.about-us__title {
  margin-bottom: 56px;

  @include vp-1199 {
    margin-bottom: 36px;
  }

  @include vp-767 {
    margin-bottom: 33px;
  }
}

.about-us__text-content {
  margin-left: -73px;
  max-width: 1000px;

  @media (min-width: 1800px) {
    margin: 0 auto;
  }

  @include vp-1199 {
    margin-left: -76px;
  }

  @include vp-767 {
    margin-left: 0;
    padding: 0 15px;
  }
}
